body {
  background-color: #dddddd;
}
.wrapper {
  display: grid;
  grid-auto-rows: 1fr;
  grid-template-columns: repeat(8, 1fr);
  margin-top: 5px;
  /* background-color: #bababa; */
}

.grid-item {
  font-size: 18px;
  grid-column-start: 3;
  grid-column-end: 7;
  background-color: #ffffff;
  padding: 20px;
}


