html, body {
  margin: 0;
  padding: 0;
}

.navbar {
  background-color: #05cfcf;
  border: 4px solid #6ba9ac;
}


@media (min-width: 720px) {
  .nav-item {
    padding: 10px;
    font-size: 30px;
  }
  .nav-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-line-pack: center;
        align-content: center;
    list-style-type: none;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-left: 20px;
    padding-right: 5px;
  }
}

@media (max-width: 720px) and (min-width: 400px){
  .nav-item {
    padding: 10px;
    font-size: 18px;
  }
  .nav-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-line-pack: center;
        align-content: center;
    list-style-type: none;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-left:20px;
    padding-right: 5px;
  }
}

@media (max-width: 400px) {
  .nav-item {
    /* padding: 5px; */
    font-size: 14px;
  }
  .nav-container {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-left: 5px;
    padding-right: 5px;
  }
}
.nav-item a {
  color: #b9edf7;
  text-decoration: none;
  text-shadow: 1px 1px 0 black;
  -ms-flex-item-align: baseline;
      align-self: baseline;
}

.nav-item span {
  color: #5b8f9c;
  text-shadow: 1px 1px 0 black;
  -ms-flex-item-align: baseline;
      align-self: baseline;
}

.name {
  -ms-flex: 1 1;
      flex: 1 1;
}


